* {
  margin: 0;
  padding: 0;
}

a {
  color: #212121;
  text-decoration: none;
}

ul {
  list-style: none;
}

address {
  font-style: normal;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 16px;
}

.pages {
  flex-direction: row;
  justify-content: center;
  display: flex;
}

.pages__item {
  height: 40px;
  width: 40px;
  background: #fff;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pages__item--movement {
  height: 40px;
  width: 40px;
  background: #f7f7f7;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pages__item:hover, .pages__item:hover a, .pages__item:focus, .pages__item:focus a, .pages__item--active, .pages__item--active a {
  color: #fff;
  background: #ff6b08;
}

.loader-modal {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  visibility: visible;
  z-index: 2;
  background-color: rgba(0, 0, 0, .2);
  justify-content: center;
  align-items: center;
  transition-property: opacity;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader-modal.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff6b08;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

